body.light-theme {
  color: var(--dominant-color);
}

body.dark-theme {
  background-image: linear-gradient(to right, rgba(0, 0, 0, .06), rgba(0, 0, 0, .06)), url("header-background-desktop-1x.a6fff9f4.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.checkbox {
  cursor: pointer;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 2;
  border: none;
  border-radius: 50px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

#switch {
  width: 100%;
  width: 60px;
  height: 25px;
  -o-transition: all .5s ease-in;
  background-color: #fff;
  border-radius: 50px;
  margin: auto 10px auto auto;
  font-size: 14px;
  font-weight: 700;
  transition: all .5s ease-in;
  display: block;
  position: relative;
  bottom: 15px;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, .5);
}

#switch.checkLight {
  background-color: #333;
}

#switch .icon {
  width: 15px;
  height: 15px;
  background-color: #e6a555;
  border-radius: 50%;
  transition: left .4s ease-out;
  position: absolute;
  top: 5px;
  left: 7px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, .5);
}

#switch:after {
  color: #333;
  opacity: 1;
  content: "Day";
  text-align: right;
  font-size: 12px;
  line-height: 40px;
  position: absolute;
  top: -7px;
  left: 30px;
}

#switch.checkLight .icon {
  width: 15px;
  height: 15px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  left: 38px;
}

#switch.checkLight .icon:before {
  opacity: 1;
  width: 13px;
  height: 13px;
  content: "";
  background-color: #333;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: -2px;
}

@keyframes leftTo {
  0% {
    opacity: 0;
    left: -20px;
  }

  50% {
    opacity: 0;
    left: -15px;
  }

  100% {
    opacity: 1;
    left: -4px;
  }
}

#switch.checkLight:before {
  color: #fff;
  content: "Night";
  text-align: left;
  opacity: 1;
  font-size: 11px;
  line-height: 40px;
  position: absolute;
  top: -7px;
  left: 6px;
}

#switch.checkLight:after, #switch:before {
  content: "";
  opacity: 0;
}

